body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navBar {
  padding: 0 3%;
  background-color: #4977ff;
}
.navbarLi {
  color: #ffce00;
  text-decoration: none;
  background-image: linear-gradient(#ffce00, #ffce00);
  background-size: 0% 0.1em;
  background-position-y: 100%;
  background-position-x: 50%;
  background-repeat: no-repeat;
  transition: background-size 0.2s ease-in-out;
}
.navbarLi:hover,
.navbarLi:focus,
.navbarLi:active {
  background-size: 100% 0.2em;
}
.navBar ul {
  margin-left: 25%;
}
.navButtons:hover {
  background-color: #ffce00;
}
.navButtons {
  border-radius: 20px;
  font-size: small;
  height: 30px;
  width: 80px;
  color: white;
  background-color: #4977ff;
  border: 2px solid white;
  cursor: pointer;
}
.logo {
  width: 50%;
  margin-left: 10px;
}
.covidTips {
  text-align: center;
}
.createEvents {
  background-color: #4977ff;
  height: 100%;
}
.main {
  overflow-y: auto;
}

.eventImage {
  display: block;
  margin-top: 20%;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
}
.createEventCol {
  margin-top: 3%;
  margin-bottom: 5%;
}
.createEventCol p,
.createEventCol h1 {
  color: white;
}
.createEventCol button {
  border-radius: 25px;
}
.whyUse {
  padding-left: 5%;
}
.whyUse h1 {
  color: #2d345a;
}
.whyUseHeading {
  text-align: center;
}
.whyUse p {
  font-size: 0.9em;
}
.reports {
  margin-left: 20px;
}
.reports h5 {
  font-weight: bold;
}
.reports div {
  padding-right: 2%;
  padding-top: 3%;
  padding-left: 3%;
}
.iframe {
  margin-top: 6%;
}
.icons {
  background-color: #ffe48f;
  border-radius: 60%;
  font-size: 50px !important;
}
.integrations {
  background-color: #39dcf1;
  clip-path: polygon(120% 18%, 140% 75%, 0 100%, 0 0);
  height: 150%;
}
.integrations h5 {
  font-weight: bold;
}
.integrations p {
  font-size: 0.9em;
}
.integrationRow {
  padding: 5% 7% 7% 2%;
}
.features {
  padding: 5%;
}
.features p {
  text-align: center;
}
.features h3 {
  color: #2d345a;
  font-weight: bold;
}
.featuresHeading {
  text-align: center;
  padding-bottom: 3%;
}
.featureIcon {
  margin-left: 50%;
  font-size: 50px !important;
}
.featureIconRow {
  padding: 2% 5% 2% 5%;
}
.contactus {
  background-color: #39dcf1;
  clip-path: polygon(100% 0, 100% 100%, 0 83%, 0 17%);
  text-align: center;
  padding: 4%;
}
.BottomBar li {
  display: inline;
}
.BottomBar {
  margin-left: 14%;
  margin-right: 14%;
}
.BottomBar li {
  cursor: pointer;
  margin: 2%;
  float: left;
}
.bottomIcons {
  text-align: center;
}
.socialIcons {
  cursor: pointer;
  margin-left: 3%;
  font-size: 30px !important;
}
.parallel18Img {
  cursor: pointer;
  display: block;
  margin-left: 42%;
  margin-right: 42%;
  width: 20%;
}
.bottomIcons p {
  margin-left: 5%;
}
